import React from "react";
import "./../../style.css";
import { useNavigate } from "react-router-dom";

import Card from "@mui/material/Card";

import CardContent from "@mui/material/CardContent";
import Grid from "@mui/material/Grid";

function DashBoard() {
  let history = useNavigate();

  function SignOut() {
    alert("You will we signed out after clicking ok");
    localStorage.clear();
    history("/login");
  }

  return (
    <div
      className="text-center"
      style={{ marginTop: "200px", textAlign: "center" }}
    >
      {/* card start */}
      <Grid
        item
        xs={12}
        sm={12}
        md={6}
        lg={4}
        container
        spacing={0}
        direction="column"
        alignItems="center"
        justify="center"
        rowSpacing={1}
        columnSpacing={{ xs: 2, sm: 2, md: 4 }}
      >
        <Card sx={{ maxWidth: 345 }}>
          {/* <CardMedia
              component="img"
              height="140"
              image="../../img/team/pt.jpg"
              alt="green iguana"
            /> */}

          <div
            className="d-flex justify-content-center"
            style={{ margin: "20px" }}
          ></div>

          <CardContent className="text-center">
            <h1
              style={{ color: "#3176E9", fontSize: "30px", fontWeight: "700" }}
            >
              {" "}
              Hi {localStorage.getItem("email")}
            </h1>
            <h1
              style={{
                color: "#3176E9",
                fontSize: "30px",
                fontWeight: "700",
                marginTop: "40px",
              }}
            >
              {" "}
              As your account is not linked to an organization, please wait
              until your school registers you to access the hub.
            </h1>

            <button
              style={{
                marginTop: "50px",
                fontSize: "20px",
                padding: "5px 20px",
                border: "1px solid #0DCAF0",
                borderRadius: "10px",
                color: "white",
                backgroundColor: "#50B3DD",
              }}
              onClick={SignOut}
              type="button"
              className=" btn-info"
            >
              Sign Out
            </button>
          </CardContent>
        </Card>
      </Grid>
      {/* card end */}
    </div>
  );
}

export default DashBoard;
