import React from "react";
import Footer from "../Footer/Footer";
import "./../../style.css";
import emailjs from 'emailjs-com'
import Header from "../../Header";


function Contact() {

  function sendEmail(e){
    e.preventDefault();
    emailjs.sendForm('service_1xl55zc','template_7su5oba',e.target,'EAlrjiCj1r-vYxTZ6')
    .then(res=>{
      console.log(res);
    }).catch(err=>{
      console.log(err);
    });
  }
  return (
    <div>
      <Header />
      <h1 style={{marginTop:"80px",textAlign:"center"}}>Contact Us</h1>

      

      <section className="contact-us">
        <div className="row">
          <div className="content-col">
            <div>
              <i className="fa fa-home"></i>
              <span>
                <h5>Sector 14</h5>
                <p>Udaipur, Rajasthan, India</p>
              </span>
            </div>
            <div>
              <i className="fa fa-phone"></i>
              <span>
                <h5>+91 9079906463</h5>
                <p>Monday To Sunday, 9AM To 6PM</p>
              </span>
            </div>
            <div>
              <i className="fa fa-envelope"></i>
              <span>
                <h5>piyush@magzigo.com</h5>
                <p>Email Us Yor Query</p>
              </span>
            </div>
          </div>
          <div className="content-col">
            <form onSubmit={sendEmail}>
              <input type="text" placeholder="Enter Name" name="name" required />
              <input type="email" placeholder="Enter Email" name="email" required />
              <input type="text" placeholder="Enter Subject" name="subject" required />
              <textarea rows="8" placeholder="Message" name="message" required></textarea>
              <button type="submit" className="hero_btn btn">
                Send Message
              </button>
            </form>
          </div>
        </div>
      </section>


      <section className="loacation">
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d116078.16294640506!2d73.63701799051773!3d24.60842606849786!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3967e56550a14411%3A0xdbd8c28455b868b0!2sUdaipur%2C%20Rajasthan!5e0!3m2!1sen!2sin!4v1649784888812!5m2!1sen!2sin"
          width="600"
          height="450"
          style={{ border: "0" }}
          allowfullscreen=""
          loading="lazy"
          referrerpolicy="no-referrer-when-downgrade"
        ></iframe>
      </section>

      <Footer/>
    </div>
  );
}

export default Contact;
