import { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./Login.css";
import logsig from "./img/logsig.png";
import Header from "./Header";

const Login = () => {
  let history = useNavigate();

  const [login_data, setData_login] = useState({
    email: "",
    password: "",
  });

  const [register_data, setData_register] = useState({
    first_name: "",
    last_name: "",
    email: "",
    password: "",
  });

  const handleChange_login = (e) => {
    setData_login({ ...login_data, [e.target.name]: e.target.value });
  };

  const handleChange_register = (e) => {
    setData_register({ ...register_data, [e.target.name]: e.target.value });
    //console.log(data);
  };

  const submitForm_login = (e) => {
    e.preventDefault();

    let formData_login = new FormData();
    formData_login.append("email", login_data.email);
    formData_login.append("password", login_data.password);

    //console.log(formData_login);
    console.log("login wla");

    axios({
      method: "POST",
      url: "https://testwebsite2911.000webhostapp.com/Login_and_signup/LoginNew.php",
      data: formData_login,
      config: { headers: { "Content-Type": "multipart/form-data" } },
    })
      .then(function (response) {
        //handle success
        console.log(response);
        if (response.data.data.Status === "Invalid") {
          localStorage.clear();
          alert("Invalid User");
        } else {
          history("/dashboard");
          localStorage.setItem("email", login_data.email);
          alert("Loggedin Successfully");
        }
      })
      .catch(function (response) {
        //handle error
        console.log(response);
      });
  };

  const submitForm_register = (e) => {
    console.log("register wla");
    e.preventDefault();
    // const sendData = {
    //   first_name: register_data.first_name,
    //   last_name: register_data.last_name,
    //   email: register_data.email,
    //   password: register_data.password,
    // };

    let formData_register = new FormData();
    formData_register.append("first_name", register_data.first_name);
    formData_register.append("last_name", register_data.last_name);
    formData_register.append("email", register_data.email);
    formData_register.append("password", register_data.password);

    //console.log(formData_register);
    //console.log(sendData);

    axios({
      method: "POST",
      url: "https://testwebsite2911.000webhostapp.com/Login_and_signup/RegisterNew.php",
      data: formData_register,
      config: { headers: { "Content-Type": "multipart/form-data" } },
    })
      .then(function (response) {
        //handle success
        //console.log(response);
        alert("New User Successfully Added.");
      })
      .catch(function (response) {
        //handle error
        console.log(response);
      });
  };

  return (
    <div className="main-box">
      <Header />
      {/* <form onSubmit={submitForm}>
        <div className="row">
          <div className="col-md-12 text-center">
            <h1>Login</h1>
          </div>
        </div>

        <div className="row">
          <div className="col-md-6">Email</div>
          <div className="col-md-6">
            <input
              type="email"
              name="email"
              className="form-control"
              onChange={handleChange}
              value={data.email}
            />
          </div>
        </div>

        <div className="row">
          <div className="col-md-6">Password</div>
          <div className="col-md-6">
            <input
              type="password"
              name="password"
              className="form-control"
              onChange={handleChange}
              value={data.password}
            />
          </div>
        </div>

        <div className="row">
          <div className="col-md-12 text-center">
            <input
              type="submit"
              name="submit"
              value="Login"
              className="btn btn-success"
            />
          </div>
        </div>
      </form> */}

      {/* <div>{localStorage.getItem("email")}</div> */}

      <div className="container" style={{ marginTop: "150px" }}>
        <input type="checkbox" id="flip" />
        <div className="cover">
          <div className="front">
            <div className="text">
              <span className="text-1">
                <img src={logsig} />
              </span>
            </div>
          </div>
          <div className="back">
            <div className="text"></div>
          </div>
        </div>
        <div className="forms">
          <div className="form-content">
            <div className="login-form">
              <div className="title">Login</div>
              <form action="#" onSubmit={submitForm_login}>
                <div className="input-boxes">
                  <div className="input-box">
                    <i className="fas fa-envelope"></i>
                    <input
                      name="email"
                      type="email"
                      onChange={handleChange_login}
                      value={login_data.email}
                      placeholder="Enter your email"
                      required
                    />
                  </div>
                  <div className="input-box">
                    <i className="fas fa-lock"></i>
                    <input
                      name="password"
                      type="password"
                      onChange={handleChange_login}
                      value={login_data.password}
                      placeholder="Enter your password"
                      required
                    />
                  </div>

                  <div className="button input-box">
                    <input type="submit" value="Submit" />
                  </div>
                  <div className="text sign-up-text">
                    Don't have an account? <label for="flip">Sigup now</label>
                  </div>
                </div>
              </form>
            </div>
            <div className="signup-form">
              <div className="title">Signup</div>
              <form action="#" onSubmit={submitForm_register}>
                <div className="input-boxes">
                  <div className="input-box">
                    <i className="fas fa-user"></i>
                    <input
                      type="text"
                      name="first_name"
                      onChange={handleChange_register}
                      value={register_data.first_name}
                      placeholder="Enter your first name"
                      required
                    />
                  </div>

                  <div className="input-box">
                    <i className="fas fa-user"></i>
                    <input
                      type="text"
                      name="last_name"
                      onChange={handleChange_register}
                      value={register_data.last_name}
                      placeholder="Enter your last name"
                      required
                    />
                  </div>

                  <div className="input-box">
                    <i className="fas fa-envelope"></i>
                    <input
                      type="text"
                      name="email"
                      onChange={handleChange_register}
                      value={register_data.email}
                      placeholder="Enter your email"
                      required
                    />
                  </div>
                  <div className="input-box">
                    <i className="fas fa-lock"></i>
                    <input
                      type="password"
                      name="password"
                      onChange={handleChange_register}
                      value={register_data.password}
                      placeholder="Enter your password"
                      required
                    />
                  </div>
                  <div className="button input-box">
                    <input type="submit" value="Submit" />
                  </div>
                  <div className="text sign-up-text">
                    Already have an account? <label for="flip">Login now</label>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
