import React from "react";
import Footer from "../Footer/Footer";
import "./../../style.css";
import Header from "../../Header";
import logo from "../../img/images/logo.png";
function Content() {
  return (
    <div>
      <Header />
      <div className="row">
        <div style={{ alignContent: "center" }}>
          <p style={{ textAlign: "center" }}>
            <img src={logo} width="50%" alt="logo" className="center" />
          </p>

          <h1>
            Magzigo acts as bridge in joining the real and the virtual world
          </h1>
          <br />
          <p>
            We act as a learning tool to tutors/ Teachers and become their
            helping hand in teaching their lovely students. K6-12 Syllabus-
            Magzigo strongly believes in the credibility of the content it
            provides and will always follow fair and true engaging syllabus
            content. <br />
            There will be timely updates to our content based on new facts and
            due to feedback from parents, teachers or students. The company will
            always get its syllabus verified by tutors. We have a team of
            professionals graduated from renowned universities like IIT and NITs
            who helps us to curate the Content development and get it further
            validated.
            <h5>
              Coming Soon- Magzigo can soon be found in the higher Medical and
              Engineering sector
            </h5>
          </p>
        </div>
      </div>

      <Footer />
    </div>
  );
}

export default Content;
