import React from "react";
import Footer from "../Footer/Footer";
import "./../../style.css";
import Team1 from "../../img/team1.png";
import Team2 from "../../img/team2.png";
import Header from "../../Header";

function About() {
  return (
    <div>
      <Header />

      <div className="container my-4">
        <div className="row featurette d-flex justify-content-center align-items-center">
          <div className="col-md-6">
            <h1>
              We believe that augmented reality will be the biggest
              technological revolution that happens in our lifetimes.
            </h1>
            <p>
              We are a education-oriented platform that uses augmented and
              virtual reality and provides both hardware and software with
              preloaded content to educational institutions on a yearly
              subscription basis. We are a full-stack business where we provide
              both software and hardware in our services but our core business
              focus lies on the software part only. Hardware is a complementary
              part to the software subscribers. We aim to transform mainstream
              education by providing the tools and asset libraries to digitize
              the content, collect data, and make a personalized learning path
              for every student.
            </p>
          </div>
          <div className="col-md-6">
            <img className="img-fluid" src={Team1} alt="" />
          </div>
        </div>
        <div className="row featurette d-flex justify-content-center align-items-center">
          <div className="col-md-6 order-md-2">
            <ul>
              <h1>Problems we are solving</h1>
              <li>
                The Lack of visual approach in subjects like Physics and
                Graphics
              </li>
              <li>The low-Quality education system for the last 100 years</li>
              <li>Exorbitant and over-priced prizes for education</li>
              <li>Lack of resources</li>
              <li>
                Theory-based education programs rather than practical experience
                followed since the industrial revolution
              </li>
              <li>Monotonous and repetitive studying</li>
            </ul>
          </div>
          <div className="col-md-6 order-md-1">
            <img className="img-fluid" src={Team2} alt="" />
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
}

export default About;
